<template>
    <div class="order">
        <van-nav-bar
            title="我的订单"
           
        />
        <div class="all-order-box">
            <div class="order-content" v-for="(item,index) in orderData" :key="index">
                <div class="movie-name">{{item.data.title}}</div>
                <div class="chair-box clearfix">
                    <div class="chair fl">
                        <ul class="fl chair-content" v-for="(item1,index1) in item.userChairData" :key="index1">
                            <li>{{item1.row}}排{{item1.col}}号</li>
                        </ul>
                    </div>
                    <div class="delect fr" @click.stop="delect(item)">删除</div>
                </div>



                <div class="footer clearfix">
                    <div class="price-box fl">总价: <span class="price">￥{{item.allPrice}}</span> </div>
                    <div class="count fl">数量:{{item.userChairData.length}}</div>
                    <div class="fr finish">交易完成</div>
                </div>

            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name:'order',
        data(){
            return {
                orderData:[]

            }
        },

        created(){
            //获取用户的信息数据
            let orderData = localStorage.getItem('orderData')
            this.orderData = orderData ? JSON.parse(orderData) : [];
            // console.log('this.orderData ==>',this.orderData)
        },

        methods:{
            delect(item){
                this.orderData.forEach(v => {
                    if(item == v){
                        this.orderData.splice(v,1)
                    }
                });
                localStorage.setItem('orderData',JSON.stringify(this.orderData));
            }        
        }
    }
</script>

<style lang="less" scoped>

.order{
    .all-order-box{
        position:fixed;
        top:46px;
        right: 0;
        bottom: 50px;
        left: 0;
        background-color: #f5f5f5;
        overflow-y: auto;
    }

    .order-content{
        padding: 10px 10px;
        margin-bottom: 10px;
        background-color: #fff;

        .movie-name{
            font-size: 20px;
            line-height: 30px;
        }
        .chair-box{
            overflow: hidden;
            margin-top: 10px;
        }
        .chair{
            width:300px;
        }
        .chair-content{
            display: block;
            
            font-size: 14px;
            margin-right: 10px;
        }
        .delect{
            width: 30px;
            height: 20px;
            border: 1px solid #000;
            text-align: center;
            line-height: 20px;
        }


        .footer{
            overflow: hidden;
            margin-top: 30px;
            font-size: 16px;;
        }
        .price{
            color: red;
        }
        .count{
            margin-left: 40px;
        }
        .finish{
            color: #B2D7A1;
        }
    }



    
}

</style>